body {
  color: #111;
  background-color: #fafafa;
  font-family: Montserrat, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media (min-width: 376px) {
  .reviews-section {
    background-color: #fafafa;
  }

  .reviews-container {
    width: 335px;
    margin: 0 auto;
  }

  .reviews-title {
    letter-spacing: -.03em;
    text-align: center;
    text-transform: uppercase;
    color: #111;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
  }

  .reviews-chocolate {
    color: #fd9222;
  }

  .reviews-list {
    flex-flow: row;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    overflow: hidden;
  }

  .reviews-list-box {
    width: 335px;
    height: 200px;
    background-color: #fff;
    border: 1px solid #1111111a;
    border-radius: 15px;
    margin-top: 74px;
    position: relative;
    box-shadow: 0 9.04932px 26.5717px #d7e4f940;
  }

  .reviews-reviewer-photo {
    justify-content: center;
    align-items: center;
    position: absolute;
    top: -40px;
    left: 132px;
  }

  .reviews-list-title {
    text-align: center;
    text-transform: capitalize;
    justify-content: center;
    padding-top: 56px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.33;
  }

  .reviews-review {
    text-align: center;
    letter-spacing: -.02em;
    color: #111111b2;
    padding: 24px 18px 42px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.29;
  }

  .reviews-slider {
    justify-content: center;
    gap: 13px;
    margin: 20px auto 32px;
    display: flex;
  }

  .reviews-slider-item {
    cursor: pointer;
    height: 13px;
    width: 13px;
    background-color: #1111111a;
    border-radius: 50%;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), width .25s cubic-bezier(.4, 0, .2, 1), border-radius .25s cubic-bezier(.4, 0, .2, 1);
    display: inline-block;
  }

  .reviews-slider-item:hover, .reviews-slider-item:focus, .reviews-slider-item:active {
    width: 28px;
    background-color: #fd9222;
    border-radius: 100px;
  }

  .reviews-button {
    color: #111;
    cursor: pointer;
    box-sizing: border-box;
    min-width: 165px;
    height: 41px;
    background-color: #0000;
    border: 1px solid #fd9222;
    border-radius: 100px;
    margin: 32px auto 72px;
    padding: 12px 28px;
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .reviews-button:hover, .reviews-button:focus {
    color: #fff;
    background-color: #fd9222;
  }

  .review-is-hidden {
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
  }

  .review-backdrop {
    width: 100%;
    height: 100%;
    background: #14152266;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 0;
    left: 0;
  }

  .reviews-modal-window {
    max-width: 311px;
    width: 95%;
    min-height: 471px;
    background: #fafafa;
    border-radius: 15px;
    padding: 32px 24px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .reviews-close-button {
    width: 24px;
    height: 24px;
    cursor: pointer;
    fill: #111;
    background-color: #0000;
    border: none;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 14px;
    right: 14px;
  }

  .reviews-close-button:hover, .reviews-close-button:focus {
    fill: #fd9222;
  }

  .reviews-close-button-svg {
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  }

  .reviews-form-message {
    letter-spacing: -.03em;
    text-align: left;
    color: #111;
    text-transform: uppercase;
    margin-bottom: 32px;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
  }

  .reviews-form {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 14px;
    display: flex;
  }

  .reviews-form-item {
    width: 100%;
    flex-direction: column;
    display: flex;
  }

  .reviews-form-label {
    display: none;
  }

  .reviews-form-input-box {
    height: 45px;
    align-items: center;
    display: flex;
    position: relative;
  }

  .reviews-form-input-box-phone {
    gap: 8px;
    display: flex;
    position: absolute;
    top: 50%;
    left: 18px;
    transform: translateY(-50%);
    box-shadow: 1.5px 0 #0000001a;
  }

  .reviews-form-input {
    width: 100%;
    height: 45px;
    background-color: #0000;
    border: 1.5px solid #1111110d;
    border-radius: 8px;
    outline: #0000;
    padding: 18px 14px;
    box-shadow: 0 5.44492px 5.44492px #00000008;
  }

  .reviews-form-input-phone {
    padding-left: 106px;
  }

  .reviews-form-icon {
    vertical-align: middle;
    width: 24px;
    height: 18px;
  }

  .reviews-form-icon-country-code {
    color: #000;
    letter-spacing: -.28px;
    vertical-align: middle;
    margin-right: 8px;
    font-size: 14px;
    font-weight: 500;
    line-height: normal;
  }

  .reviews-form-textarea {
    width: 100%;
    min-height: 91px;
    letter-spacing: -.02em;
    resize: none;
    background-color: #0000;
    border: 1.5px solid #1111110d;
    border-radius: 8px;
    outline: #0000;
    padding: 14px 18px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.17;
    box-shadow: 0 5.44492px 5.44492px #00000008;
  }

  .reviews-form-input::placeholder, .reviews-form-textarea::placeholder {
    color: #4040404d;
    letter-spacing: -.02em;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.17;
  }

  .reviews-send-button {
    letter-spacing: normal;
    color: #fff;
    cursor: pointer;
    box-sizing: border-box;
    min-width: 93px;
    height: 41px;
    background-color: #fd9222;
    border: none;
    border-radius: 100px;
    padding: 12px 28px;
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.17;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .reviews-send-button:focus, .reviews-send-button:hover {
    color: #fd9222;
    background-color: #fcf1e0;
  }
}

