:root {
  --main-color: #111;
  --accent-background: #1e1823;
  --logo-color: #fd9222;
  --background-color: #fafafa;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .section {
    padding: 96px 0;
  }

  .backdrop {
    width: 100%;
    height: 100%;
    background: var(--background-color);
    opacity: 1;
    pointer-events: all;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    inset: 0 0 auto;
  }

  .is-hidden {
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
  }

  .modal {
    width: 311px;
    height: 994px;
    background: var(--background-color);
    border-radius: 15px;
    padding: 32px 24px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 50%;
    left: 50%;
    overflow: auto;
    transform: translate(-50%, -50%);
    box-shadow: 0 1px 1px #00000024, 0 1px 3px #0000001f, 0 2px 1px #0003;
  }

  .close-btn {
    cursor: pointer;
    width: 24px;
    height: 24px;
    color: #111;
    border: 1px solid #0000001a;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 24px;
    right: 24px;
  }

  .close-btn:hover, .close-btn:focus {
    border: none;
  }

  .div-modal-form {
    margin-bottom: 18px;
  }

  .label-modal {
    color: #000;
    letter-spacing: -.28px;
    padding-bottom: 8px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 18px;
    display: block;
  }

  .div-label-modal {
    position: relative;
  }

  .input-modal, .personal-information {
    width: 263px;
    letter-spacing: -.36px;
    background-color: #0000;
    border: 1.5px solid #1111110d;
    border-radius: 13px;
    outline: #0000;
    padding: 14px 18px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .svg-modal {
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);
  }

  .modal-div-four {
    margin-bottom: 16px;
  }

  .textarea-modal {
    width: 263px;
    letter-spacing: -.36px;
    resize: none;
    color: #4040404d;
    background-color: #0000;
    border: 1.5px solid #1111110d;
    border-radius: 13px;
    outline: #0000;
    padding: 14px 18px 60px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .svg-modal-ua {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
  }

  .svg-modal-mastercard {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }

  .btn-serv {
    min-width: 169px;
    height: 56px;
    background-color: var(--logo-color);
    cursor: pointer;
    border: none;
    border-radius: 4px;
    margin: 0 auto;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .btn-order {
    letter-spacing: .04em;
    background-color: var(--logo-color);
    color: #fff;
    cursor: pointer;
    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
  }

  .btn-order:hover, .btn-order:focus {
    background-color: #404bbf;
    box-shadow: 0 4px 4px #00000026;
  }

  .team {
    background-color: #f4f4fd;
    padding: 120px 0;
  }

  .information {
    text-transform: capitalize;
    letter-spacing: .02em;
    text-align: center;
    color: #2e2f42;
    font-size: 36px;
    line-height: 1.11;
  }

  .list {
    margin: 0;
    padding-left: 0;
    list-style: none;
  }

  .our-team-ul {
    flex-direction: column;
    justify-content: center;
    gap: 16px;
    margin-bottom: 40px;
    display: flex;
  }

  .our-team-li {
    width: 160px;
    background: #fff;
    border: 1px solid #1111111a;
    border-radius: 15px;
    padding: 24px 20px 18px;
  }

  .gram {
    background: var(--background-color);
    border: 1px solid #1111111a;
    border-radius: 100px;
    padding: 8px 27px;
  }

  .gram:focus, .gram:hover {
    box-shadow: 0 4px 4px #00000026;
  }

  .team-info {
    text-align: center;
  }
}

